body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100%;
  background-color:rgb(39, 94, 22);
  text-align: center;
  font-size: 2rem;
  color:rgb(200, 255, 0) ;
}

.Header{
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10%;
  background-color: blue;
}

.headerImage {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10%;
}

.game {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10%;
}

.instructions{
  font-size: 1.5rem;
}
.instruction-text1{
  font-size: 1rem
}
.instruction-text2{
  font-size: 1rem
}


